body {
  margin: 0;
  font: normal 75% Arial, Helvetica, sans-serif;
}
a {
  color: inherit;
  text-decoration: none;
}
ul {
  padding: 0px;
}
li {
  padding: 15px 0px;
  font-size: 1rem;
  line-height: 1rem;
}

#tsparticles {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: #010b1b;
}

.App {
  width: 95vw;
  padding: 10px;
  margin: auto;
  position: relative;
  z-index: 1;
  box-sizing: border-box;
  color: #fff;
}

@media only screen and (min-width: 1290px) {
  .App {
    width: 70vw;
  }
}
